import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import './styles/App.css';
import Sidebar from "./components/Sidebar"
import Home from "./views/Home"
import About from "./views/About"
import Projects from "./views/Projects"
import Resume from "./views/Resume"
import Teaching from "./views/Teaching"

function App() {
  return (
    <Router>
      <Sidebar />
        <div>
          <Switch>
            <Route exact path="/"><Home /></Route>
            <Route exact path="/about" component={About} when="always" />
            <Route exact path="/projects" component={Projects} when="always" />
            {/* <Route exact path="/teaching" component={Teaching} when="always" /> */}
            <Route exact path="/resume" component={Resume} when="always" />
          </Switch>
        </div>
    </Router>
  );
}

export default App;
