import React, { useState } from 'react';
import '../styles/Sidebar.css';
import { AiFillLinkedin, AiFillGithub, AiFillMail } from "react-icons/ai"

function LinkButton(props) {
    return (
        <div class="linkbox">
            <a href={props.link}><div class="link">{props.title}</div></a>
        </div>
    );
}

function Sidebar() {
  const [liColor, onHoverLI] = useState("white");
  const [gitColor, onHoverGIT] = useState("white");
  const [mailColor, onHoverMAIL] = useState("white");
  

  function hoverIcon(icon) {
      if (icon == "li") {
          onHoverLI("gray");
      } else if (icon == "git") {
          onHoverGIT("gray");
      } else if (icon == "mail") {
          onHoverMAIL("gray");
      } else {
        onHoverLI("white");
        onHoverGIT("white");
        onHoverMAIL("white");
      }
  }

  return (
    <div class="Sidebar">
        <div class="links">
            {/*<Icon/>*/}
            <LinkButton link={"/"} title={"Home"}/>
            <LinkButton link={"/about"} title={"About"}/>
            <LinkButton link={"/projects"} title={"Projects"}/>
            {/* <LinkButton link={"/teaching"} title={"Teaching"}/> */}
            <LinkButton link={"/resume"} title={"Resume"}/>
        </div>
        <div class="socials">
            <div class="spacer1"></div>
            <a href="https://www.linkedin.com/in/derekynzhang/"><AiFillLinkedin onMouseOver={() => hoverIcon("li")} onMouseOut={() => hoverIcon("undo")} color={liColor} size={25}/></a>
            <a href="https://github.com/derek-yn-zhang"><AiFillGithub onMouseOver={() => hoverIcon("git")} onMouseOut={() => hoverIcon("undo")} color={gitColor} size={25}/></a>
            <a href="mailto:derek.yn.zhang@gmail.com"><AiFillMail onMouseOver={() => hoverIcon("mail")} onMouseOut={() => hoverIcon("undo")} color={mailColor} size={25}/></a>
            <div class="spacer2"></div>
        </div>
    </div>
  );
}

export default Sidebar;
