import React, { useState } from 'react';
import '../styles/General.css';
import Logo from '../img/background.jpeg';

function Background(props) {
    if (props.include) {
        return <img class="cover" src={Logo} alt='cover'/>;
    } else {
        return null;
    }
}

function General(props) {
    return (
        <body>
            <div class="canvas">
                <Background include={props.include} />
                <h1 class="name">{props.title}</h1>
            </div>
        </body>
    );
}

export default General;