import React, { useState } from 'react';
import '../styles/Projects.css';
import General from "../components/General"
import molecule from '../img/icon/molecule.svg';
import personality from '../img/icon/personality.svg';
import pills from '../img/icon/pills.svg';
import mobile from '../img/icon/mobile.svg';
import chess from '../img/icon/chess.svg';
import matthew from '../img/icon/matthew.svg';

// , overflowWrap: "break-word", hyphens: "auto"

function Description(props) {
    if (props.side === 'right') {
        return (
            <div class="descript" style={{textAlign: "right", marginRight: "9%"}}>
                <p class="title" style={{color: "white", fontWeight: "bold"}} >{props.title}</p>
                <p class="summary" style={{fontSize: 13}} >{props.summary}</p>
            </div>
        );
    } 
    else if (props.side === 'left') {
        return (
            <div class="descript" style={{textAlign: "left", marginLeft: "9%"}}>
                <p class="title" style={{color: "white", fontWeight: "bold"}} >{props.title}</p>
                <p class="summary" style={{fontSize: 13}} >{props.summary}</p>
            </div>
        );
    }
}

function Icon(props) {
    return (
        <a href={props.link}>
            <div class="responsiveIcon">
                <img class="icon" src={props.src} alt='icon'/>
            </div>
        </a>
    );
}

function Projects() {
    return (
        <div class="canvas">
            <General />
            <div class="projects">
                <div class="content">

                    <div class="right">
                        <div id="nortop" class="project">
                            <Description side={"right"} title={"Generative Models for Molecular Design"} 
                                        summary={"GPU-accelerated, web-based AI application for de novo drug design. \
                                        VAE is present for interpolation, VAE-GAN for realism, and RL for optimization. \
                                        Models written in Python using PyTorch, Keras, and CUDA. Web application built with \
                                        React and deployed on Amazon EC2 servers."}/>
                            <Icon src={molecule} link={""}/>
                        </div>
                        <div class="project">
                            <Description side={"right"} title={"Personality Profiles of Drug Users"} 
                                        summary={"Aggregates and visualizes the personality features of drug users, classified across various drug partitions. \
                                        Data cleaning and computational methods performed in R, visualization created using the D3 javascript library."}/> 
                            <Icon src={personality} link={"https://derek-yn-zhang.github.io/vizh/"}/>
                        </div>
                        <div class="project">
                            <Description side={"right"} title={"Personalized Math Education Software"} 
                                        summary={"Open-source, web-based learning technology for elementary mathematics students. \
                                        The question bank is fashioned after the DeepMind mathematics database, and question difficulty is calibrated \
                                        with learning curves derived from the Additive Factors Model of learning."}/>
                            <Icon src={matthew} link={""} />
                        </div>
                    </div>

                    <div class="left">
                        <div id="noltop" class="project">
                            <Icon src={pills} link={""} />
                            <Description side={"left"} title={"Machine Learning for the Opioid Crisis"} 
                                        summary={"Explores socio-demographic and psychometric constructs as factors of the opioid crisis \
                                        and discusses the potential role of predictive risk models in developing more effective \
                                        intervention and prescription policies. EDA and ML models implemented in R."}/>
                        </div>
                        <div class="project">
                            <Icon src={mobile} link={"https://github.com/derek-yn-zhang/ingress-android"}/>
                            <Description side={"left"} title={"Location-based Mobile Application"} 
                                        summary={"Multiplayer AR game written in Java with the Google Maps SDK for Android. \
                                        Game mechanics and responsive map display implemented using asynchronous HTTP requests with \
                                        JSON formatted data. "}/>
                        </div>
                        <div class="project">
                            <Icon src={chess} link={""} />
                            <Description side={"left"} title={"Stochastic Chess Visualization"} 
                                        summary={"Viewing the results of over 4 million simulated random chess games using D3 and Matplotlib. \
                                        Random walks are visualized with embeddings generated by non-linear dimensionality reduction techniques. \
                                        Animated figures include an evolving heat map of captures and visits. "}/>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
}

export default Projects;